/* Importing vendor stylesheets */

@import 'node_modules/uswds/src/stylesheets/uswds.scss';
@import 'node_modules/slick-carousel/slick/slick.scss';
@import 'node_modules/slick-carousel/slick/slick-theme.scss';

/*
**
** Custom VA Styles
**
**
** DIRECTORY:
**
** 1. Settings - Any project variables that are used globally: colors, fonts, etc.
**    1a. Variables
**
** 2. Tools - Any mixins, animations, or functions used throughout the project.
**    2a. Animations
**    2b. Mixins
**    2c. Overlay
**    2d. Utilities
**
** 3. Generic - This is where reset/normalize & box-sizing styles go.
**
** 4. Elements - Bare HTML elements are styled in this section (<h1>, <a>, <p>, etc.)
**    4a. Typography
**    4b. Inputs
**    4c. Separators
**
** 5. Objects - Non-cosmetic design patterns including grid and layout classes
**    5a. Spacing
**
** 6. Components - Specific pieces of UI that are stylized. This typically ends up being where the majority of your sheets are created.
**    6a. Accordion
**    6b. Alert
**    6c. Button
**    6d. Button-menu
**    6e. Cards
**    6f. Carousel
**    6g. Colors
**    6h. Date-picker
**    6i. File-upload
**    6j. Header
**    6k. Lists
**    6l. Loading-indicator
**    6m. Load-more
**    6n. Modal
**    6o. Navigation
**    6p. Pagination
**    6q. Range-input
**    6r. Status-indicators
**    6s. Table
**    6t. Tabs
**    6u. Time-picker
**    6v. Typeahead
**
** 7. Trumps - Contains anything that beats/"trumps" all other layers, ie fixes, utility and helper classes, and other overrides.
**
*/


/* 1. Settings */
@import '_settings/variables';

/* 2. Tools */
@import '_tools/animations';
@import '_tools/mixins';
@import '_tools/overlay';
@import '_tools/table-sortable';
@import '_tools/utilities';

/* 3. Generic */

/* 4. Elements */
@import '_elements/typography';
@import '_elements/inputs';
@import '_elements/separators';

/* 5. Objects */
@import '_objects/spacing';

/* 6. Components */
@import '_components/accordion';
@import '_components/alert';
@import '_components/button';
@import '_components/button-menu';
@import '_components/cards';
@import '_components/carousel';
@import '_components/colors';
@import '_components/date-picker';
@import '_components/file-upload';
@import '_components/header';
@import '_components/lists';
@import '_components/loading-indicator';
@import '_components/load-more';
@import '_components/modal';
@import '_components/navigation';
@import '_components/pagination';
@import '_components/popover';
@import '_components/process-list';
@import '_components/range-input';
@import '_components/status-indicators';
@import '_components/table';
@import '_components/tabs';
@import '_components/time-picker';
@import '_components/typeahead';

/* 7. Trumps */
