/* ----------------------------- */
/* Alerts
/* ----------------------------- */

.usa-alert-success,
.usa-alert-info,
.usa-alert-error,
.usa-alert-warning {
  background-image: none;
  position: relative;

  &:after {
    font-family: FontAwesome;
    position: absolute;
    font-size: 3.6rem;
    top: 2rem;
    left: 2rem;
    margin: 0;
  }

  .usa-alert-body {
    padding-left: 5rem;
  }

  .usa-checklist {
    list-style-type: disc;
    list-style-position: outside;
    margin: 0;
    padding: 0;
  }

  .usa-checklist li {
    display: list-item;
    list-style-type: disc;
    list-style-position: outside;
    margin-left: 20px;
    padding: 0;
    text-indent: 0;
  }
  .usa-checklist li:before {
    display: none;
  }
}

.usa-alert-success:after {
  content: $fa-check-circle;
}

.usa-alert-info:after {
  content: $fa-info-circle;
}

.usa-alert-error:after {
  content: $fa-exclamation-circle;
}

.usa-alert-warning:after {
  content: $fa-exclamation-triangle;
}