/* ----------------------------- */
/* Modal
/* ----------------------------- */

$modal-background-color: $color-white;
$modal-close-color: #6e747e;
$modal-shadow-size: 0px;
$modal-shadow-blur: 4px;
$modal-shadow-color: rgba(0, 0, 0, 0.50);
$modal-border: $vaux-border;
$modal-max-width: 600px;
$modal-max-height: 215px;
$modal-padding: 1.2em;

[data-modal-trigger] {
  cursor: pointer;
}

.vaux-modal {
  display: none;
  opacity: 0;
  background: $modal-background-color;
  @include shadow($modal-shadow-size, $modal-shadow-blur, $modal-shadow-color);
  border-radius: 3px;
  padding-bottom: $modal-padding;
  position: fixed;
  top: 1.2em;
  left: 50%;
  transform: translate(-50%, 0);
  width: 80%;

  &.modal--open {
    display: block;
    z-index: 1000;
  }

  /* close Button */
  .vaux-modal-close {
    background-color: transparent;
    border: 0;
    border-radius: 0;
    font-weight: 400;
    outline: 0;
    -webkit-font-smoothing: auto;
    float: right;
    text-align: center;
    color: $modal-close-color;
    display: block;
    height: auto;
    width: auto;
    margin: initial;
    margin-bottom: 0.8rem;
    padding: 0;
    
    /* icon */
    &:after {
      content: $fa-times;
      display: inline-block;
      font-family: FontAwesome;
      margin-left: .5rem;
      padding: 0;
      text-decoration: inherit;
      height: 13px;
      width: 17px;
    }
  }

  @include media($tablet-screen){
    max-width: $modal-max-width;
  }
}

.vaux-modal-header {
  padding: 0.9em $modal-padding 0;

  h1, h2, h3, h4, h5, h6, p {
    margin-top: 0;
    margin-bottom: 0;
  }
}

.vaux-modal-content {
  padding: 0em $modal-padding;
}

.vaux-modal-footer {
  padding: $modal-padding $modal-padding 0 $modal-padding;

  @include media($medium-screen) {
    button {
      margin-bottom: 0;
      margin-top: 0;
    }
  }
}

/* Complex Modal Styles */
.vaux-modal--complex {
  position: absolute;
  overflow: hidden;
  box-shadow: none;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  transform: none;
  padding-bottom: 0;

  .vaux-modal-header {
    position: fixed;
    top: 0;
    z-index: 100;
    background-color: $modal-background-color;
    width: 100%;
    border-bottom: $modal-border;
    padding: 0.9em $modal-padding;
  }

  .vaux-modal-content {
    position: absolute;
    top: 0;
    bottom: 0;
    overflow-y: scroll;
  }

  .vaux-modal-footer {
    position: fixed;
    bottom: 0;
    padding-bottom: 1.2em;
    background-color: $modal-background-color;
    width: 100%;
    border-top: $modal-border;
  }

  @include media($tablet-screen) {
    position: fixed;
    height: auto;
    width: auto;
    top: 1.2em;
    left: 50%;
    transform: translate(-50%, 0);
    max-width: $modal-max-width;

    .vaux-modal-header, .vaux-modal-footer {
      position: relative;
    }

    .vaux-modal-content {
      position: relative;
      max-height: $modal-max-height;
      overflow-y: scroll;
    }
  }  
}