/* ----------------------------- */
/* Navigation
/* ----------------------------- */

$nav-menu-background-color: $color-white;
$nav-header-background-color: $color-gray-lightest;
$nav-border-color: $color-gray-warm-light;
$nav-text-color: $color-black;
$nav-font-weight: $font-normal;

/* Nav Wrapper */
.vaux-main-nav {

  /* menu button */
  .usa-menu-btn {
    display: block;
    text-transform: none;
    font-size: 1.5rem;
    font-weight: 900;
    width: 8.7rem;
    height: 3.9rem;
    padding: 0 1.5rem;
    .fa {
      margin-left: 0.5rem;
    }
  }
}

/* Menu Wrapper */
.vaux-nav-menu-wrapper {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 1000;
  background: $nav-menu-background-color;
  border-left: 1px solid $nav-border-color;
  width: 100%;
  transform: translate3d(100%, 0, 0);
  transition: transform 0.3s;

  @include media($medium-screen) {
    width: 300px;
  }
  
  /* When menu is open */
  &.menu--open {
    transform: translate3d(0, 0, 0);
  }

  /* top navbar */
  .usa-navbar {
    background-color: $nav-header-background-color;
    border-bottom: 1px solid $nav-border-color;
    height: 5rem;
    padding: 0rem 2rem;

    @media screen and (min-width: 951px) {
      display: block;
    }
  }

  /* close Button */
  .usa-nav-close {
    color: $nav-text-color;
    height: auto;
    width: auto;
    margin: 0;
    padding: 1.65rem 0;
    
    /* icon */
    &:after {
      content: $fa-times;
      display: inline-block;
      font-family: FontAwesome;
      margin-left: .5rem;
      display: inline-block;
      height: 12px;
      width: 17px;
    }

    @media screen and (min-width: 951px) {
      display: block;
    }
  }

  /* back button */
  .vaux-menu-back {
    position: absolute;
    z-index: 100;
    background: none;
    color: $nav-text-color;
    font-weight: $nav-font-weight;
    margin: 0;
    padding: 1.65rem 0;
    transition: opacity 0.1s;
    width: auto;

    &:before {
      content: '';
      display: inline-block;
      width: 15px;
      height: 12px;
      background: url('#{$vaux-img-path}angle-left.png') no-repeat left -5px center;
      background: url('#{$vaux-img-path}angle-left.svg') no-repeat left -5px center, linear-gradient(transparent, transparent);
      background-size: 17px;
    }

    &:hover, &:focus, &:active {
      text-decoration: underline;
    }
    
    /* hidden when on the main menu */
    &.vaux-menu-back--hidden {
      pointer-events: none;
      opacity: 0;
    }
  }
}

/* List Wrapper */
.vaux-nav-menus {
  position: absolute;
  top: 3.5em;
  bottom: 0;
  overflow: hidden;
  width: 100%;

  /* search bar */
  .usa-search [type=search], .usa-search .usa-search-input {
    width: calc(100% - 9.5rem);
  }

  /* lists/menus */
  .usa-sidenav-list {
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
    overflow-y: scroll;
    visibility: hidden; 
    list-style-type: none;
    height: 83%;
    width: calc(100% + 50px);
    margin: 0;
    padding: 0.3rem 2rem;
    margin-bottom: -0.5rem;

    &[class^='animate-'],
    &[class*=' animate-'] {
      visibility: visible;
    }

    &:focus {
      outline: none;
    }

    h3 {
      margin-top: 0;
    }

    & > li:first-of-type {
      border-top: none;
    }

    &.switch-apps-submenu {
      height: 100%;
    }
  }
  
  /* current/active list */
  .usa-sidenav-list--current {
    visibility: visible;
  }
}

/* List items */
.vaux-menu-item {
  display: block;
  width: calc(100% - 50px);
}

/* Links within list items */
.vaux-menu-link {
  position: relative;
  display: block;
  -webkit-transition: color 0.1s;
  transition: color 0.1s;

  &[data-submenu]:after {
    position: absolute;
    right: 0;
    top: 30%;
    content: '';
    display: block;
    font-family: FontAwesome;
    font-size: 1.7rem;
    font-style: normal;
    font-weight: normal;
    height: 17px;
    padding-right: .5rem;
    text-align: right;
    width: 17px;
    content: $fa-angle-right;
  }
}

/* Application Menu */
.usa-sidenav-list.application-menu {
  background-color: $color-gray-lightest;
  padding: 1.8rem 2rem;
  top: auto;
  bottom: 5px;
  height: auto;
  visibility: visible;
  border-top: 1px solid $color-gray-warm-light;
  > li {
    border-color: $color-gray-light;
  }
  .vaux-menu-link {
    padding-left: 0;
    &:hover i {
      color: $color-primary;
    }
    i {
      margin-right: 1.3rem;
      width: 17px;
      color: $color-primary-darker;
    }
  }
}

.switch-apps-submenu {
  z-index: 1;
  background-color: white;
  li {
    a {
      padding: 1.5rem 0.8rem;
    }
    img {
      width: 40px;
      height: 40px;
      display: inline-block;
      position: absolute;
    }
    div {
      display: inline-block;
      padding-left: 6rem;
      h5 {
        margin-top: 0;
        margin-bottom: 0.6rem;
      }
      p {
        margin-bottom: 0;
        margin-top: 0;
      }

    }
  }
}

/* Menu Item Animations */
.animate-outToRight {
  .vaux-menu-item, h3, .usa-search {
    animation: outToRight 0.25s both ease-in-out;
  }
}

.animate-outToLeft {
  .vaux-menu-item, h3, .usa-search {
    animation: outToLeft 0.25s both ease-in-out;
  }
}

.animate-inFromLeft {
  .vaux-menu-item, h3, .usa-search {
    animation: inFromLeft 0.25s both ease-in-out;
  }
}

.animate-inFromRight {
  .vaux-menu-item, h3, .usa-search {
    animation: inFromRight 0.25s both ease-in-out;
  }
}