/* ----------------------------- */
/* Button Menus
/* ----------------------------- */

$button-menu-background-color: $color-white;
$button-menu-border: $vaux-border;
$button-menu-hover: $color-gray-lighter;
$button-menu-link-color: $color-black;
$button-menu-link-padding: 1rem 1rem;
$button-menu-group-label-color: $color-gray;

.vaux-button-menu-wrapper {
  display: inline-block;
  position: relative;
  width: 100%;

  &.align-menu-right {
    text-align: right;
    .vaux-button-menu {
      text-align: left;
      right: 0;
    }
  }

  button {
    margin-right: 0;
    margin-bottom: 3px;
    &:after {
      content: $fa-caret-down;
      display: inline-block;
      width: 17px;
      height: 10px;
      color: inherit;
      font-family: FontAwesome;
      font-weight: normal;
    }
  }

  @include media($small-screen) {
    max-width: 350px;
  }
}

/* Main action button on left and Button Menu action on right */
.vaux-button-split {
  position: relative;
  
  button {
    width: auto;

    &:first-of-type {
      width: 85%;
      margin-right: -6px;
      position: relative;
      top: -1px;
      height: 38px;
      &:after {
        display: none;
      }
    }

    &:last-of-type {
      width: 15%;
      padding: 1rem 1.15rem;
      border-left: solid 1px rgba($color-black, 0.25);
      border-top-left-radius: 0px;
      border-bottom-left-radius: 0px;
      position: relative;

      &:before {
        content: '';
        background-color: rgba($color-black, 0.25);
        position: absolute;
        right: 0;
        top: 0;
        bottom: 0;
        left: 0;
      }

      &:after {
        position: relative;
      }
    }

    @include media($small-screen) {
      &:first-of-type {
        margin-right: -6px;
        width: auto;
      }
      &:last-of-type { width: auto; }
    }
  } 
} 

/* Button Menu dropdown content (hidden by default) */
.vaux-button-menu {
  display: none;
  background-color: $button-menu-background-color;
  border-radius: 3px;
  box-shadow: 1px 1px 4px 0 rgba(0, 0, 0, 0.3);
  border: $button-menu-border;
  z-index: 500;
  position: absolute;
  max-width: 100%;
  min-width: 44%;
  width: 100%;
  ul {
    margin: 0;
    padding-left: 0;
  }
  li {
    list-style: none;
    a {
      color: $button-menu-link-color;
      display: block;
      text-decoration: none;
      line-height: 1;
      padding: $button-menu-link-padding;
      &:hover {
        background-color: $button-menu-hover;
      }
    }
  }
  &.show {
    display: block;
  }

  @include media($small-screen) {
    width: auto;
  }
}

.vaux-button-menu_group {
  border-top: $button-menu-border;
}

.vaux-button-menu_group-label {
  padding: $button-menu-link-padding;
  padding-bottom: 0.5rem;
  font-size: 1.3rem;
  display: block;
  line-height: 1.3;
  color: $button-menu-group-label-color;
}
