/* ----------------------------- */
/* Accordion
/* ----------------------------- */

.usa-accordion-button {
  padding: 1.5rem 3rem 1.5rem 5.5rem;
  background-image: none;

  &:after {
    content: $fa-minus;
    font-family: FontAwesome;
    position: absolute;
    left: 3rem;
  }
}

.usa-accordion-button[aria-expanded='false'] {
  background-image: none;
  &:after {
    content: $fa-plus;
  }
}