/* ----------------------------- */
/* Separators
/* ----------------------------- */

.va-h-ruled--stars {
  display: flex;
  justify-content: center;
  align-items: center;
  background: url('#{$vaux-img-path}stars.png') no-repeat center;
  background-size: 11rem auto;
  margin: 1.6rem auto auto;
  padding: 2rem 0 0;
  text-align: center;

  &:before,
  &:after {
    border-top: $vaux-border;
    content: " ";
    flex: 1 1 50%;
    padding: 1rem 0;
  }

  /* creates the white BG gap */
  &:before {
    margin-right: 12rem;
  }
}

@media screen and (min-width: $tablet-screen) {
  .va-h-ruled--stars {
    padding-left: 0;
  }
}

.heading-border-top {
  margin-top: 1.5em;
  border-top: 2px solid $color-gray-light;
  padding: 1em 0;
}