/* ----------------------------- */
/* Typography
/* ----------------------------- */

/* Overwriting some $font-serif declarations to $font-sans */
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: $font-sans;
}

/* Overwriting heading sizes to match what is on the USWDS site */
h1 {font-size: $h1-font-size;}
h2 {font-size: $h2-font-size;}
h3 {font-size: $h3-font-size;}
h4 {font-size: $h4-font-size;}
h5 {font-size: $h5-font-size;}
h6 {font-size: $h6-font-size;}

.usa-font-lead {
  font-family: $font-sans;
  font-weight: 300;
}

/* Transferring this style from USWDS for heading alt style */
.usa-display-alt {
  font-size: 4.4rem;
  font-weight: 300;
}

.usa-heading-alt {
  border-bottom: 1px solid $color-gray;
  padding-bottom: .8rem;
}

/* Buttons/Text that should appear like links */
.vaux-link {
  @extend a;
  font-weight: 500;
  -webkit-font-smoothing: auto;
  padding: 0;
  margin: 0;
  width: auto;
  line-height: 2.2rem;
  background-color: transparent;
  &:hover, &:active {
    background-color: transparent;
    text-decoration: underline;
  }
}

// Changing out USWDS SVG for FontAwesome icon
.usa-external_link:after {
  background: none;
  content: $fa-external-link;
  color: inherit;
  font-family: FontAwesome;
  font-size: 1.15rem;
  margin-right: 0.4rem;
}