/* ----------------------------- */
/* File Upload Input
/* ----------------------------- */

$file-upload-border: $vaux-border;
$file-upload-progress-bar-bg: #dce4ee;
$file-upload-progress-bar-fill: #0071bb;

/* Upload Button */
.vaux-file-upload-wrapper { 
  label {
    display: inline-block;
    span {
      margin: 0;
    }
  }

  input[type="file"] {
    display: none;
  }

  .hidden {
    display: none;
  }
}

/* File List */
.vaux-file-list {
  list-style: none;
  padding-left: 0;
  margin-bottom: 0;
}

.vaux-file-item {
  border-top: $file-upload-border;
  margin: 0;
  padding: 1rem 1.2rem;
  position: relative;
  &:last-of-type {
    border-bottom: $file-upload-border;
  }

  ul.file-actions {
    float: right;
    list-style: none;
    margin: 0;
    li {
      display: inline-block;
      margin-bottom: 0;
      & + li {
        margin-left: 1.3rem;
      }
      &.hidden {
        display: none;
      }
    }
  }

  div.file-info {
    max-width: 43%;
    display: inline-block;
    
    span:first-of-type {
      word-wrap: break-word;
    }

    @include media($tablet-screen) {
      max-width: 60%;
    }
  }
}

.vaux-file-list.no-borders .vaux-file-item {
  border: 0;
  padding: 0;
}

/* Upload Process */
progress[value] {
  /* reset the default appearance */
  -webkit-appearance: none;
  appearance: none;
  margin-top: 1rem;
  margin-bottom: 0.4rem;
  display: block;
  height: 1.2rem;
  width: 100%;

  &::-webkit-progress-bar {
    background: $file-upload-progress-bar-bg;
    border-radius: 6px;
  }

  &::-webkit-progress-value {
    background-color: $file-upload-progress-bar-fill;
    border-radius: 6px 0 0 6px;
  }
}