/* ----------------------------- */
/* Inputs
/* ----------------------------- */

input[type="range"] {
  height: auto;
}

fieldset.usa-fieldset-inputs {
  legend {
    display: block;
    max-width: 46rem;
    font-size: 1.7rem;
    font-weight: normal;
    margin-bottom: 1rem;
  }
}

fieldset.usa-fieldset-inputs.usa-input-error {
  legend { 
    margin-top: 3rem;
    margin-bottom: -1rem;
    & + .usa-input-error-message {
      margin-bottom: 0.5rem;
    }
  }
}

