/* ----------------------------- */
/* Date Picker Input
/* ----------------------------- */

$datepicker-border-radius: 0.3rem;
$datepicker-box-shadow: 0px 1px 4px 0 rgba(0, 0, 0, 0.3);
$datepicker-header-bg-color: $color-primary;
$datepicker-header-text-color: $color-white;
$datepicker-header-fg-color: $color-primary-darker;
$datepicker-dow-color: $color-primary;
$datepicker-active-color: $color-primary-darkest;
$datepicker-active-text-color: $color-white;
$datepicker-disabled-color: $color-gray-lighter;
$datepicker-hover-color: $color-cool-blue-lightest;
$datepicker-highlight-color: $color-cool-blue-lightest;
$datepicker-today-color: $color-primary-darker;
$datepicker-old-new-dates-color: $color-gray-light;

/* Input */
.vaux-date-picker-input {
  position: relative;
  max-width: 46rem;

  input {
    border-radius: 0 $datepicker-border-radius $datepicker-border-radius 0;
    max-width: 100%;
  }

  > button {
    position: absolute;
    right: 0;
    top: 0;
    height: 4.4rem;
    width: auto;
    border-radius: 0 $datepicker-border-radius $datepicker-border-radius 0;
    margin: 0;     
  }
}

/* Calendar */
.vaux-datepicker {
  max-width: 340px;
  width: 100%;
  
  table {
    table-layout: fixed;
    box-shadow: $datepicker-box-shadow;
    border-radius: $datepicker-border-radius;
    margin: 0;
    width: 100%;
    user-select: none;
    tbody td,
    tbody th,
    thead tr:not(.datepicker-header) th {
      border: none;
      border-radius: $datepicker-border-radius;
      text-align: center;
      padding: 0.6rem;
      width: 100%;
    }
    
    /* Table Cells & Year Spans */
    td {
      &.day {
        &:hover, &:focus, &.focused {
          background: $datepicker-hover-color;
          cursor: pointer;
        }
      }
      
      &.old,
      &.new {
        opacity: 0;
        pointer-events: none;
        &:hover {
          cursor: default;
        }
      }

      &.new {
        display: none;
      }

      &.disabled,
      &.disabled:hover {
        background: none;
        color: $datepicker-disabled-color;
        cursor: default;
      }

      &.highlighted {
        background: $datepicker-highlight-color;
        border-radius: 0;
      }

      &.active,
      &.active:hover,
      &.active.disabled,
      &.active.disabled:hover {
        background-color: $datepicker-active-color;
        color: $datepicker-active-text-color;
      }

      &.today,
      &.today:hover,
      &.today.disabled,
      &.today.disabled:hover {
        position: relative;
        &:after {
          display: inline-block;
          position: absolute;
          bottom: -8px;
          right: -1px;
          font: normal normal normal 20px/1 FontAwesome;
          content: $fa-caret-right;
          color: $datepicker-today-color;
          transform: rotate(45deg);
        }
        &.active {
          background-color: $datepicker-hover-color \9;
        }
      }

      &.range,
      &.range:hover,
      &.range.disabled,
      &.range.disabled:hover {
        background: #eee;
        border-radius: 0;
        &.today {
          background-color: #f3d17a;
          background-image: linear-gradient(to bottom, #f3c17a, #f3e97a);
          background-repeat: repeat-x;
          border-color: #f3e97a #f3e97a #edde34;
          border-color: rgba(0, 0, 0, 0.1) rgba(0, 0, 0, 0.1) rgba(0, 0, 0, 0.25);
          border-radius: 0;
        }
      }

      &.selected,
      &.selected:hover,
      &.selected.disabled,
      &.selected.disabled:hover {
        background-color: #9e9e9e;
        background-image: linear-gradient(to bottom, #b3b3b3, #808080);
        background-repeat: repeat-x;
        border-color: #808080 #808080 #595959;
        border-color: rgba(0, 0, 0, 0.1) rgba(0, 0, 0, 0.1) rgba(0, 0, 0, 0.25);
        color: #fff;
        text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.25);
        &.active, &:active {
          background-color: #808080;
        }
      }

      span {
        display: block;
        height: 34px;
        line-height: 34px;
        float: left;
        margin: 8px 7px;
        padding: 0 8px;
        cursor: pointer;
        border-radius: $datepicker-border-radius;
        &:hover, &.focused {
          background: $datepicker-hover-color;
        }

        &.disabled, &.disabled:hover {
          background: none;
          color: #999;
          cursor: default;
        }

        &.active,
        &.active:hover,
        &.active.disabled,
        &.active.disabled:hover {
          background-color: $datepicker-active-color;
          color: $datepicker-active-text-color;
        }

        &.old,
        &.new {
          color: #999;
        }
      }
    }

    /* Table Footer (Year view) */
    tfoot {
      tr {
        th {
          border: none;
          padding: 0px 2.1rem;
          padding-bottom: 1.7rem;
          button {
            margin: 0;
            width: 48%;
            padding: 0.25rem 2rem;
            &:first-of-type {
              margin-right: 1rem;
            }
          }
        }
      }
    }
  }
}

/* Calendar -- Header */
.datepicker-header {
  background-color: $datepicker-header-bg-color;
  color: $datepicker-header-text-color;
  
  th {
    background-color: transparent;
    text-align: center;
    border: none;
    
    i {
      background-color: $datepicker-header-fg-color;
      color: inherit;
      padding: 0.75rem 1.5rem;
    }

    &:not(.datepicker-switch) {
      padding: 1.25rem;
    }

    &.prev, &.next {
      width: 14%;
      cursor: pointer;
    }

    &.prev {
      padding-right: 0 !important;
      border-top-left-radius: $datepicker-border-radius;
      i {
        border-top-left-radius: $datepicker-border-radius;
        border-bottom-left-radius: $datepicker-border-radius;
      }
    }

    &.next {
      padding-left: 0 !important;
      
      i {
        margin-left: -2px;
        border-top-right-radius: $datepicker-border-radius;
        border-bottom-right-radius: $datepicker-border-radius;
      }
    }
  }

  .datepicker-switch {
    padding: $datepicker-border-radius 0;
    width: 145px;
    cursor: pointer;
    &:last-of-type {
      padding-right: 1.25rem;
      border-top-right-radius: $datepicker-border-radius;
    }

    &:hover {
      background: transparent;
    }

    .vaux-datepicker-header-month,
    .vaux-datepicker-header-year {
      background-color: $color-primary-darker;
      display: block;
      padding-top: 0.6rem;
      padding-bottom: 0.4rem;
    }

    .vaux-datepicker-header-month {
      padding-right: 1rem;
    }

    .vaux-datepicker-header-year {
      padding-top: 0;
      padding-bottom: 0;
      padding-left: 1rem;
      border-radius: 0.3rem;
      i {
        padding-left: 1.25rem;
        padding-right: 1rem;
      }
    }
  }
}

/* Days view */
.datepicker-days {
  .dow {
    background-color: transparent;
    color: $datepicker-dow-color;
  }
}


/* Right to left support */
.datepicker.datepicker-rtl {
  direction: rtl;
  table tr td span {
    float: right;
  }
}

/* Dropdown support */
.datepicker-dropdown {
  top: 0;
  left: 0;
  &:before {
    content: '';
    display: inline-block;
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    border-bottom: 7px solid #999;
    border-top: 0;
    border-bottom-color: rgba(0, 0, 0, 0.2);
    position: absolute;
  }
  &:after {
    content: '';
    display: inline-block;
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;
    border-bottom: 6px solid #fff;
    border-top: 0;
    position: absolute;
  }

  /* Orientation support */
  &.datepicker-orient-left {
    &:before {
      left: 6px;
    }
    &:after {
      left: 7px;
    }
  }

  &.datepicker-orient-right {
    &:before {
      right: 6px;
    }
    &:after {
      right: 7px;
    }
  }

  &.datepicker-orient-bottom {
    &:before {
      top: -7px;
    }
    &:after {
      top: -6px;
    }
  }

  &.datepicker-orient-top {
    &:before {
      bottom: -7px;
    }
    &:after {
      bottom: -6px;
    }
  }
}

/* Striped table support */
.table-striped .datepicker table tr td,
.table-striped .datepicker table tr th {
  background-color: transparent;
}

/* Input variant support */
.vaux-datepicker .cw {
  font-size: 10px;
  width: 12px;
  padding: 0 2px 0 5px;
  vertical-align: middle;
}

.input-append.date,
.input-prepend.date {
  .add-on {
    cursor: pointer;
    i {
      margin-top: 3px;
    }
  }
}

.input-daterange {
  input {
    text-align: center;
    &:first-child {
      border-radius: $datepicker-border-radius 0 0 $datepicker-border-radius;
    }
    &:last-child {
      border-radius: 0 $datepicker-border-radius $datepicker-border-radius 0;
    }
  }

  .add-on {
    display: inline-block;
    width: auto;
    min-width: 16px;
    height: 18px;
    padding: 4px 5px;
    font-weight: normal;
    line-height: 18px;
    text-align: center;
    text-shadow: 0 1px 0 #fff;
    vertical-align: middle;
    background-color: #eee;
    border: 1px solid #ccc;
    margin-left: -5px;
    margin-right: -5px;
  }
}

