/* ----------------------------- */
/* Header
/* ----------------------------- */

/* App Header */
.usa-header {
  border-bottom: none;

  .usa-navbar {
    background-color: $color-primary-darkest;
    width: 100%;
    height: 4rem;
  }

  .usa-logo a {
    color: $color-white;
    font-size: 1.5rem;
    font-weight: 300;
    img {
      width: 18px;
    }
  }

  .usa-search {
    float: none;
    margin-bottom: initial;
    max-width: none;
  }
  
  /* Large Header Styles */
  &.usa-header--large {
    > .usa-navbar {
      height: 6.4rem;
      .usa-menu-btn {
        margin: 1.5rem;
        border-radius: 0.3rem;
        height: 3.4rem;
        line-height: 3.4rem;
      }
    }

    .usa-logo {
      line-height: 6.4rem;
      a {
        display: block;
        line-height: 6.4rem;
      }
      img {
        width: 72px;
        vertical-align: middle;
      }
    }
  }
}

/* Govt Banner */
.usa-header {
  .usa-banner .usa-banner-button {
    margin-left: 5px;
    padding: 1.3rem 0 0 4.8rem;
    vertical-align: text-bottom;

    &:after {
      background-image: none;
      font-family: FontAwesome;
      font-size: 1.6rem;
      content: $fa-angle-down;
      position: relative;
      left: auto;
      height: 10px;
      right: auto;
      top: 2px;
    }

    &[aria-expanded=true]:after {
      content: $fa-times;
      right: 2.1rem;
      top: 1.9rem;
      position: absolute;
      color: $color-gray;
      font-size: 1.9rem;
    }
  }

  @include media($small-screen) {
    .usa-banner .usa-banner-button {
      padding: 0;

      &[aria-expanded=true]:after {
        content: $fa-angle-up;
        right: auto;
        position: relative;
        color: inherit;
        top: 2px;
      }
    }
  }
}