/* ----------------------------- */
/* Time Picker Input
/* ----------------------------- */

$timepicker-bg-color: $color-white;
$timepicker-time-bg-color: $color-gray-warm-light;
$timepicker-box-shadow: 1px 1px 4px 0 rgba(0, 0, 0, 0.3);
$timepicker-border: $vaux-border;
$timepicker-border-radius: 0.3rem;

/* Input */
.vaux-time-picker-input {
  position: relative;
  max-width: 46rem;

  input {
    border-radius: 0 $timepicker-border-radius $timepicker-border-radius 0;
    max-width: 100%;
  }

  > button {
    position: absolute;
    right: 0;
    top: 0;
    height: 4.4rem;
    width: auto;
    border-radius: 0 $timepicker-border-radius $timepicker-border-radius 0;
    margin: 0;     
  }

  /* orientation helper */
  &.pull-right {
    .vaux-timepicker-widget {
      &.dropdown-menu {
        left: auto;
        right: 0;

        &:before {
          left: auto;
          right: 12px;
        }
        &:after {
          left: auto;
          right: 13px;
        }
      }
    }
  }
}

/* Time Table */
.vaux-timepicker-widget {
  border: $timepicker-border;
  border-radius: $timepicker-border-radius;
  box-shadow: $timepicker-box-shadow;
  padding: 2rem 1.3rem;
  position: absolute;
  z-index: 100;
  background-color: $timepicker-bg-color;
  top: auto !important; /* Overwriting calculations from JavaScript */
  left: auto !important; /* Overwriting calculations from JavaScript */

  &.dropdown-menu {
    &.open {
      display: inline-block;
    }
  }

  /* orientation helpers */
  &.timepicker-orient-left {
    &:before {
     left: 6px;
    }
    &:after {
     left: 7px;
    }
  }

  &.timepicker-orient-right {
    &:before {
     right: 6px;
    }
    &:after {
     right: 7px;
    }
  }

  &.timepicker-orient-top {
    &:before {
     top: -7px;
    }
    &:after {
      top: -6px;
    }
  }

  &.timepicker-orient-bottom {
    &:before {
      bottom: -7px;
      border-bottom: 0;
      border-top: 7px solid #999;
    }
    &:after {
      bottom: -6px;
      border-bottom: 0;
      border-top: 6px solid #ffffff;
    }
  }

  table {
    width: 100%;
    margin: 0;

    td {
      padding: 0;
      border: none;
    }

    tr.vaux-timepicker-time-row {
      td {
        padding: 9px 0px;
      }
    }

    a {
      padding: 0 7px;
    }

    button {
      margin: 0 7px;
      padding: 0.5rem 2rem;
      width: auto;
    }

    input {
      margin: 0 auto;
      width: 49px;
      background-color: $timepicker-time-bg-color;
      border: none;
      border-radius: $timepicker-border-radius;
      font-size: 2.2rem;
      text-align: center;
      padding: 9px 0px;
    }
  }

  .modal-content {
    padding: 4px;
  }
}
