/* ----------------------------- */
/* Process List
/* ----------------------------- */

$dot-background-color: $color-primary;
$dot-text-color: $color-white;
$dot-checked-background-color: $color-gray-lighter;
$dot-checked-text-color: $color-white;
$dot-ring-color: $color-white;
$dot-connector-color: $color-gray-lighter;
$dot-padding: 1rem;
$dot-size: 24px;

// process list - vertical (default)
.vaux-process-list {
  ol {
    counter-reset: stepCount;
    list-style: none;
    margin: 0;
    max-width: 300px;
    padding: 0 0 0 12px;

    li {
      border-left: 2px $color-gray-lighter solid;
      min-height: 60px;
      padding-bottom: 1rem;
      position: relative;
    }

    li::before {
      color: white;
      background: $dot-background-color;
      border-radius: 50%;
      counter-increment: stepCount;
      content: counter(stepCount);
      font-size: 13px;
      height: $dot-size;
      left: -13px;
      line-height: 26px;
      position: absolute;
      text-align: center;
      top: 0;
      width: $dot-size;
      box-shadow: 0 0 0 10px $dot-ring-color;
    }

    li.checked {
      &::before {
        content: "\f00c";
        font-family: FontAwesome;
      }
    }

    li:last-child {
      border: 0;
    }

    li.current ~ li::before {
      background-color: $dot-checked-background-color;
    }

    li span {
      display: block;
      margin-left: 32px;
    }

    li button {
      margin-top: 0;
      margin-bottom: 30px;
    }
  }
}

// process list - horizontal
.vaux-process-list.horizontal {
  ol {
    display: flex;
    max-width: 100%;
    padding: 12px 50px 0;

    li {
      border-left: 0;
      border-top: 2px $color-gray-lighter solid;
      display: flex;
      flex-basis: 150px;
      flex-grow: 1;
      min-height: 0;
      min-width: 60px;
      padding-bottom: 12px;
      vertical-align: top;
    }

    li::before {
      left: 0;
      top: -12px;
    }

    li:last-child {
      border: 0;
      flex-basis: 0;
      flex-grow: 0;
      min-width: 0;
      width: auto;
    }

    li span {
      left: -50px;
      margin: 0;
      max-width: 140px;
      min-width: 120px;
      position: relative;
      text-align: center;
      top: 20px;
      white-space: nowrap;
      z-index: 99;
    }

  }
}

// process list - bottom label
.vaux-process-list.has-bottom-label-activated {
  ol li span {
      display: none;
  }
  .process-bottom-label {
    font-weight: bold;
    min-width: 286px;
    padding: 0 0 0 4%;
    text-align: center;
  }
}